@import url('https://fonts.googleapis.com/css2?family=Jacques+Francois&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IM+Fell+DW+Pica+SC&display=swap');
@import url('https://maxcdn.bootstrapcdn.com/bootstrap/3.3.5/css/bootstrap.min.css');

.current {
  color: #d88aa3;
}

body {
 background-image: url("/assets/bg.jpg");
 font-family: 'Jacques Francois', serif;
 color: #685E4E;
 margin:0;
 padding:0;
 height:100%;
 text-align: center;
}

p {
	text-align:justify;
}

li {
  text-align: justify;
}

.index-updates li {
  text-align: center;
}

ul {
  text-align: center;
  list-style-type: none;
}


/* Mobile Portrait Size to Mobile Landscape Size (devices and browsers) */
@media only screen and (max-width: 749px) {

nav {
 text-align: center;
 font-family: 'IM Fell DW Pica SC', serif;
 font-size: 18px;
 color: #685E4E;
 word-spacing: 16px;
}
nav ul li{
  display: inline;
  font-size: 18px;
}

 .mobile-hide {
    display: none !important;
 }

.container-fluid {
    width:100%;
    margin-left:auto;
    margin-right:auto;
    position:relative;

}

.wardrobe {
    width:100%!important;
}

.img-responsive {
    display: block;
    max-width: 100%;
    width: 100%;
    height: auto;
}


}





/* Mobile Portrait Size to Mobile Landscape Size (devices and browsers) */
@media only screen and (max-width: 1024px) {
.col-md-4, .col-md-8 {
  width:100%!important;
} 

nav {
 text-align: center;
 font-family: 'IM Fell DW Pica SC', serif;
 font-size: 18px;
 color: #685E4E;
 word-spacing: 16px;
}
nav ul li{
  display: inline;
  font-size: 18px;
}

 .mobile-hide {
    display: none !important;
 }

.container-fluid {
    width:85%;
    margin-left:auto;
    margin-right:auto;
    position:relative;

}

.wardrobe {
    width:100%!important;
}


}


/* Mobile Portrait Size to Mobile Landscape Size (devices and browsers) */
@media only screen and (min-width: 1025px) {
nav {
 background-image: url("/assets/menu-bg.png");
 width: 175px;
 height: 250px;
 text-align: center;
 font-family: 'IM Fell DW Pica SC', serif;
 margin-top: 75px;
 font-size: 18px;
 padding-top: 10px;
 background-repeat: no-repeat;
 color: #685E4E;
 list-style-type: none;
}

.container-fluid {
    width:75%;
    margin-left:auto;
    margin-right:auto;
    position:relative;
    min-height: 100%; 
    text-align: center;
}

.right {
   float:right!important
}

.wardrobe {
    width:90%!important;
}

}



a {
	color: #685E4E;
	text-decoration: underline;
}

a:hover {
	color: #9e917c;
	text-decoration: underline;
}

nav a {
	text-decoration: none;
	
}





#header {
	width: 100%;
	padding-top: 0;
	height:31px;
	background: url("/assets/border.png");
}

#footer {
   height:60px;
   background: url("/assets/border.png");
   background-repeat: repeat-x;
   margin-top: 20px;
}



.about {
   text-align: left;
}


.pagination {
  display: inline-block;
  word-spacing: 3px;
  font-weight: 12px;
}


.pagination a {
  float: left;
  padding: 8px 8px;
  text-decoration: none;
}

.pagination a.selected {
  text-decoration: underline;
}

.wrapper { 
   min-height:100vh;
   position:relative;
   }

.table {
    border-bottom:0px !important;
}
.table th, .table td {
    border: 1px !important;
    text-align: center;
}
.fixed-table-container {
    border:0px !important;
}

.img-responsive {width: auto;
max-width: 100%;}


.img-hor {
    -moz-transform: scale(1, 1);
    -o-transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
}

.comment-heading {
  font-family: 'Quicksand', sans-serif;
  font-size: 1.5rem;
  text-align: center;
  color: #b86b77;
  margin-top: 2rem;
  margin-bottom: 1rem;
  letter-spacing: 1px;
}

.comment-heading {
  position: relative;
  display: inline-block;
}

.comment-heading::after {
  content: '';
  display: block;
  width: 80%;
  margin: 0.5rem auto 0;
  border-bottom: 2px dashed #b86b77;
}


.comment-note {
  font-family: 'Quicksand', sans-serif;
  font-size: 1rem;
  color: #aa6b84;
  text-align: center;
  margin-top: 0.5rem;
  margin-bottom: 2rem;
  opacity: 0.8;
  line-height: 1.4;
}
